import store from "@/store";

/**
 * @description 
 *
 * @param {Array} routes
 * @param {String} permissions
 */
export const filterAsyncRoutes = (routes, permissions) => {
  if (hasRootPermission(permissions)) {
    return routes;
  }
  const res = [];
  routes.forEach(route => {
    const tmp = { ...route };
    if (hasPermission(tmp, permissions)) {
      if (tmp.children) {
        tmp.children = filterAsyncRoutes(tmp.children, permissions);
      }
      res.push(tmp);
    }
  });

  return res;
};

/**
 * @description 
 *
 * @param {Array} routes
 * @param {String} permissions
 */
const hasPermission = (route, permissions) => {
  if (route.meta && route.meta.permissions) {
    return permissions.indexOf(route.meta.permissions) > -1;
  } else {
    return true;
  }
};

/**
 * @description 
 */
const hasRootPermission = (permissions, root = "*") => {
  return permissions.indexOf(root) > -1;
};

/**
 * @description 
 */
export const checkPermission = permission => {
  const permissions = store.state.auth.permissions;
  if (hasRootPermission(permissions)) {
    return true;
  }
  return permissions.indexOf(permission) > -1;
};
