import request from "@/plugins/request";
import { LOGIN, USER, LOGOUT, REFRSH } from "@/enums/routes";

/**
 * @description 
 */
const login = data => {
  return request.post(LOGIN, data);
};

/**
 * @description 
 */
const user = () => {
  return request.get(USER);
};

/**
 * @description 
 */
const logout = () => {
  return request.post(LOGOUT);
};

const refresh = () => {
  return request.get(REFRSH);
};

export default {
  login,
  user,
  logout,
  refresh
};
