import RoleAPI from "@/api/role";

const state = {
  list: [],
  meta: {},
  links: {},
  item: {},
  all: []
};

const getters = {
  total: state => {
    return state.meta.total;
  }
};

const mutations = {
  SET_LIST: (state, permissions) => {
    state.list = permissions;
  },
  SET_META: (state, meta) => {
    state.meta = meta;
  },
  SET_LINKS: (state, links) => {
    state.links = links;
  },
  ADD_ITEM_TO_LIST: (state, item) => {
    state.list.push(item);
  },
  SET_ITEM: (state, detail) => {
    state.detail = detail;
  },
  UPDATE_ITEM: (state, payload) => {
    state.list.map(item => {
      if (item.id === payload.id) {
        item.name = payload.name;
        item.display_name = payload.display_name;
        return;
      }
    });
  },
  DELETE_ITEM: (state, payload) => {
    state.list.map((item, index) => {
      if (item.id === payload.id) {
        state.list.splice(index, 1);
        return;
      }
    });
  },
  SET_ALL: (state, all) => {
    state.all = all;
  }
};

const actions = {
  async get({ commit }, payload) {
    const response = await RoleAPI.index(payload);
    const { data, links, meta } = response;
    commit("SET_LIST", data);
    commit("SET_LINKS", links);
    commit("SET_META", meta);
    return response;
  },
  async store({ commit }, payload) {
    const response = await RoleAPI.store(payload);
    commit("ADD_ITEM_TO_LIST", payload);
    return response;
  },
  async find({ commit }, payload) {
    const response = await RoleAPI.show(payload.id);
    const { data } = response.data;
    commit("SET_ITEM", data);
    return response;
  },
  async update({ commit }, payload) {
    const { id, data } = payload;
    const response = await RoleAPI.update(id, data);
    commit("UPDATE_ITEM", {
      id: id,
      name: data.name,
      display_name: data.display_name
    });
    return response;
  },
  async delete({ commit }, payload) {
    const response = await RoleAPI.destroy(payload.id);
    commit("DELETE_ITEM", payload);
    return response;
  },
  async all({ commit }) {
    const response = await RoleAPI.all();
    commit("SET_ALL", response.data);
    return response;
  }
};

export default {
  namespaced: true,
  state,
  mutations,
  actions,
  getters
};
