import axios from "axios";
import { MessageBox } from "element-ui";
import store from "@/store";
import { getToken } from "@/utils/token";
import router from "@/router";

const service = axios.create({
  baseURL: "/api",
  timeout: 30000,
  headers: {
    Accept: "application/json",
    "X-Requested-With": "XMLHttpRequest"
  }
});

service.interceptors.request.use(
  config => {
    if (store.getters.token) {
      config.headers["Authorization"] = "Bearer " + getToken();
    }
    return config;
  },
  error => {
    console.log(error); // for debug
    return Promise.reject(error);
  }
);

service.interceptors.response.use(
  response => {
    return response.data;
  },
  error => {
    const code = error.response.data.code;
    const message = error.response.data.message;
    switch (code) {
      case 401: // 
        MessageBox.confirm(message, "", {
          confirmButtonText: "",
          cancelButtonText: "",
          type: "warning"
        })
          .then(() => {
            store.dispatch("auth/refresh").then(() => {
              location.reload(); // vue-router bug
            });
          })
          .catch(() => {
            store.dispatch("auth/resetToken");
          });
        return Promise.reject(error);
      case 403:
        router.push("/403");
        return Promise.reject(error);
      case 404:
        router.push("*");
        return Promise.reject(error);
    }
    return Promise.reject(error);
  }
);

export default {
  /**
   * @description get 
   *
   * @param {*} url
   * @param {*} params
   * @returns
   */
  get(url, params) {
    return new Promise((resolve, reject) => {
      service({
        url: url,
        method: "get",
        params
      })
        .then(response => {
          resolve(response);
        })
        .catch(error => {
          reject(error);
        });
    });
  },

  /**
   * @description post 
   *
   * @param {*} url
   * @param {*} data
   * @returns
   */
  post(url, data) {
    return new Promise((resolve, reject) => {
      service({
        url: url,
        method: "post",
        data
      })
        .then(response => {
          resolve(response);
        })
        .catch(error => {
          reject(error);
        });
    });
  },

  /**
   * @description put 
   *
   * @param {*} url
   * @param {*} data
   * @returns
   */
  put(url, data) {
    return new Promise((resolve, reject) => {
      data["_method"] = "PUT";
      service({
        url: url,
        method: "post",
        data
      })
        .then(response => {
          resolve(response);
        })
        .catch(error => {
          reject(error);
        });
    });
  },

  /**
   * @description delete 
   *
   * @param {*} url
   * @param {*} [data={}]
   * @returns
   */
  delete(url, data = {}) {
    return new Promise((resolve, reject) => {
      data["_method"] = "DELETE";
      service({
        url: url,
        method: "post",
        data
      })
        .then(response => {
          resolve(response);
        })
        .catch(error => {
          reject(error);
        });
    });
  }
};
