import request from "@/plugins/request";
import { ADMIN } from "@/enums/routes";

const index = params => {
  return request.get(ADMIN, params);
};

const store = data => {
  return request.post(ADMIN, data);
};

const show = id => {
  id = Number(id);
  return request.get(`${ADMIN}/${id}`);
};

const update = (id, data) => {
  id = Number(id);
  return request.put(`${ADMIN}/${id}`, data);
};

const destroy = id => {
  id = Number(id);
  return request.delete(`${ADMIN}/${id}`);
};

export default {
  index,
  store,
  show,
  update,
  destroy
};
