import request from "@/plugins/request";
import { PERMISSION } from "@/enums/routes";

const index = params => {
  return request.get(PERMISSION, params);
};

const destroy = id => {
  return request.delete(`${PERMISSION}/${id}`);
};

const store = data => {
  return request.post(PERMISSION, data);
};

const update = (id, data) => {
  return request.put(`${PERMISSION}/${id}`, data);
};

const show = id => {
  return request.get(`${PERMISSION}/${id}`);
};

const all = () => {
  return request.get(`${PERMISSION}/all`);
};

export default {
  index,
  store,
  show,
  update,
  destroy,
  all
};
